@import url("https://fonts.googleapis.com/css?family=Lato:400,700");

:root {
  --primaryColor: #2c23f8;
  --mainWhite: #fff;
  --mainBlack: #222;
  --mainGrey: #ececec;
  --mainSpacing: 0.1rem;
  --mainTransition: all 0.3s linear;
  --mainRadius: 0.5rem;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  color: var(--mainBlack);
  background: var(--mainWhite);
  font-family: "Lato", sans-serif;
}

.navbar {
  background: var(--mainWhite);
  position: sticky;
  top: 0;
  height: 60px;
  width: 100%;
  display: flex;
  align-items: center;
  background: rgb(231, 226, 221);
  z-index: 1;
  .navbar-center {
    width: 100%;
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1.5rem;
    .nav-icon {
    font-size: 1.5rem;
    }
    .logo {
    width: 80px;
    height: 40px;
    }
    .cart-btn {
    position: relative;
    cursor: pointer;
    }
    .cart-items {
    position: absolute;
    top: -8px;
    right: -8px;
    background: var(--primaryColor);
    padding: 0 5px;
    border-radius: 30%;
    color: var(--mainWhite);
    }
  }
}

.hero {
  min-height: calc(100vh - 60px);
  background: url("../images/hero-bcg.jpeg") center/cover no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  .banner {
    text-align: center;
    background: rgba(255, 255, 255, 0.8);
    border-radius: var(--mainRadius);
    display: inline-block;
    padding: 2rem;
    .banner-title {
      font-size: 3.4rem;
      text-transform: uppercase;
      letter-spacing: var(--mainSpacing);
      margin-bottom: 3rem;
    }
  }
}
.banner-btn {
  padding: 1rem 3rem;
  text-transform: uppercase;
  letter-spacing: var(--mainSpacing);
  font-size: 1rem;
  background: var(--primaryColor);
  border-radius: var(--mainRadius);
  color: var(--mainBlack);
  border: 1px solid var(--primaryColor);
  transition: var(--mainTransition);
  cursor: pointer;
  &:hover {
    background: transparent;
    color: var(--primaryColor);
  }
}

.products {
  padding: 4rem 0;
  .section-title h2 {
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 5rem;
    text-transform: capitalize;
    letter-spacing: var(--mainSpacing);
  }
}
.products-center {
  width: 90vw;
  margin: 0 auto;
  max-width: 1170px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  grid-column-gap: 1.5rem;
  grid-row-gap: 2rem;
  .product {
    h3 {
      text-transform: capitalize;
      font-size: 1.1rem;
      margin-top: 1rem;
      letter-spacing: var(--mainSpacing);
      text-align: center;
    }
    h4 {
      margin-top: 0.7rem;
      letter-spacing: var(--mainSpacing);
      color: var(--primaryColor);
      text-align: center;
    }
  }
}
.img-container {
  position: relative;
  overflow: hidden;
  .product-img {
    display: block;
    width: 100%;
    min-height: 1.2rem;
    transition: var(--mainTransition);
    &:hover {
      opacity: 0.5;
    }
  }
}
.bag-btn {
  position: absolute;
  top: 75%;
  right: 0;
  background: var(--primaryColor);
  border: none;
  border-radius: var(--mainRadius);
  text-transform: uppercase;
  padding: 0.5rem 0.75rem;
  letter-spacing: var(--mainSpacing);
  font-weight: bold;
  transition: var(--mainTransition);
  transform: translateX(101%);
  cursor: pointer;
  &:hover {
    color: var(--mainWhite);
  }
  .fa-shopping-cart {
    margin-right: 0.5rem;
  }
}
.img-container:hover .bag-btn {
  transform: translateX(0);
}

.cart-overlay {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  transition: var(--mainTransition);
  background: rgb(240, 157, 81, 0.5);
  z-index: 2;
  visibility: hidden;
}
  .cart {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    overflow: scroll;
    z-index: 3;
    background: rgb(231, 226, 221);
    padding: 1.5rem;
    transition: var(--mainTransition);
    transform: translateX(100%);
    h2 {
      text-transform: capitalize;
      text-align: center;
      letter-spacing: var(--mainSpacing);
      margin-bottom: 2rem;
    }
  }

.showCart {
  transform: translateX(0);
}
.transparentBcg {
  visibility: visible;
}
@media screen and (min-width: 768px) {
  .cart {
    width: 30vw;
    min-width: 450px;
  }
}
.close-cart {
  font-size: 1.7rem;
  cursor: pointer;
}

.cart-item {
  display: grid;
  align-items: center;
  grid-template-columns: auto 1fr auto;
  grid-column-gap: 1.5rem;
  margin: 1.5rem 0;
  img {
    width: 75px;
    height: 75px;
  }
  h4 {
    font-size: 0.85rem;
    text-transform: capitalize;
    letter-spacing: var(--mainSpacing);
  }
  h5 {
    margin: 0.5rem 0;
    letter-spacing: var(--mainSpacing);
  }
}
.item-amount {
  text-align: center;
}
.remove-item {
  color: grey;
  cursor: pointer;
}
.fa-chevron-up,
.fa-chevron-down {
  color: var(--primaryColor);
  cursor: pointer;
}

.cart-footer {
  margin-top: 2rem;
  letter-spacing: var(--mainSpacing);
  text-align: center;
  h3 {
    text-transform: capitalize;
    margin-bottom: 1rem;
  }
}